/*!
 * Art styles
 */
body {
  position: relative;
  font-size: 18px;
  line-height: 1.45;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
}

h1, h2, h3, h4, h5 {
  color: black;
  font-family: 'PT Sans Narrow', sans-serif;
  font-weight: 700;
  width: 100%;
  margin: 1em 0;
}

h1 {
  font-size: 48px;
}

@media (min-width: 576px) {
  h1 {
    font-size: 64px;
  }
}

@media (min-width: 768px) {
  h1 {
    font-size: 74px;
  }
}

@media (min-width: 992px) {
  h1 {
    font-size: 79px;
  }
}

@media (min-width: 1200px) {
  h1 {
    font-size: 83px;
  }
}

h2 {
  font-size: 27px;
}

@media (min-width: 576px) {
  h2 {
    font-size: 29px;
  }
}

@media (min-width: 768px) {
  h2 {
    font-size: 32px;
  }
}

@media (min-width: 992px) {
  h2 {
    font-size: 38px;
  }
}

@media (min-width: 1200px) {
  h2 {
    font-size: 43px;
  }
}

h3 {
  font-size: 25px;
}

h4 {
  font-size: 20px;
}

p, ul, li {
  line-height: 1.4;
}

p {
  margin: 0;
  padding: 0 0 1em;
}

b, strong {
  font-weight: bold;
}

.pagination {
  margin: 25px 0;
  text-align: center;
  width: 100%;
}

.pagination .screen-reader-text {
  display: none;
}

.pagination .page-numbers {
  display: inline-block;
  border: none;
  margin-right: 5px;
  line-height: 32px;
  width: 32px;
  border-radius: 50%;
  transition: background 0.3s ease;
  background: #f06c00;
  color: #fff;
  text-decoration: none;
}

.pagination .page-numbers:hover {
  background: #bd5500;
  transition: background 0.3s ease;
}

.pagination .page-numbers.current {
  background: #4c2c12;
  color: #fff;
}

.pagination .page-numbers.dots:hover {
  background: none;
}

body.tax-type .header-layout {
  min-height: 500px;
}

.header-layout {
  min-height: 600px;
  position: relative;
  color: #fff;
}

.header-layout .description {
  font-size: 19px;
  font-family: "PT Sans Narrow", sans-serif;
  text-align: center;
}

@media (min-width: 576px) {
  .header-layout .description {
    font-size: 23px;
  }
}

@media (min-width: 768px) {
  .header-layout .description {
    font-size: 26px;
  }
}

@media (min-width: 1200px) {
  .header-layout .description {
    font-size: 30px;
    text-align: left;
  }
}

.header-layout .page-title {
  color: #fff;
  text-align: center;
  text-shadow: 2px 4px 4px rgba(0, 0, 0, 0.42);
}

@media (min-width: 1200px) {
  .header-layout .page-title {
    text-align: left;
  }
}

.header-layout:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.header-layout .brand-telephone {
  background: white;
  padding: 12px 26px;
  border-radius: 30px;
  color: black;
  line-height: 1;
}

.header-layout .brand-telephone a {
  text-decoration: none;
  color: black;
}

.header-layout .brand-telephone a:visited {
  color: #000;
}

.header-layout .brand-logo {
  line-height: 1;
  display: block;
