@media (min-width: 576px) {
  .header-layout .brand-name {
    display: block;
    font-size: 16px;
  }
}

.header-layout .service {
  font-size: 16px;
  color: #ecc69b;
  text-align: center;
}

@media (min-width: 1200px) {
  .header-layout .service {
    text-align: left;
  }
}

.header-layout .slogan {
